import "./GameOnline.css";
import { useEffect, useState } from "react";
import data from "./services/data";

function GameOnline({ player }) {
  const [game, setGame] = useState();
  const [player1, setPlayer1] = useState();
  const [player2, setPlayer2] = useState();

  useEffect(() => {
    data.getAllGames().then((response) => {
      setGame(
        response.find(
          (g) => g.player1Id == player.id || g.player2Id == player.id
        )
      );
      data.getPlayer(game.player1Id).then((player1R) => {
        if (!player1R.username) {
          setPlayer1({ ...player1R, username: "Player 1" });
        } else {
          setPlayer1(player1R);
        }
        data.getPlayer(game.player2Id).then((player2R) => {
          if (!player2R.username) {
            setPlayer2({ ...player2R, username: "Player 2" });
          } else {
            setPlayer2(player2R);
          }
        });
      });
    });
  }, []);

  return (
    <>
      <h1>GameOnline</h1>
      <h2>
        {game == true && player1 == true && player2 == true
          ? `${player1.username} vs. ${player2.username}`
          : ""}
      </h2>
    </>
  );
}
export default GameOnline;
